  list-style: none;
}

body {
  min-height: 100vh;
  line-height: 1.5;
}

h1, h2, h3, h4, button, input, label {
  line-height: 1.1;
}

h1, h2, h3, h4 {
  text-wrap: balance;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: currentColor;
}

img, picture {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

textarea:not([rows]) {
  min-height: 10em;
}

:target {
  scroll-margin-block: 5ex;
}

:root {
  --sl-color-primary-50: #f9f9fe;
  --sl-color-primary-100: #ebebfc;
  --sl-color-primary-200: #dbdcf9;
  --sl-color-primary-300: #cacbf6;
  --sl-color-primary-400: #b3b5f3;
  --sl-color-primary-500: #9598ee;
  --sl-color-primary-600: #7578e9;
  --sl-color-primary-700: #575ae4;
  --sl-color-primary-800: #4346cc;
  --sl-color-primary-900: #303292;
  --sl-color-primary-950: #1e1f5a;
  --lookbook-toolbar-height: 40px;
  --lookbook-toolbar-icon-button-size: 18px;
  --lookbook-toolbar-icon-button-color: var(--sl-color-neutral-400);
  --lookbook-divider-color: var(--sl-color-neutral-300);
}

html, body {
  font-family: var(--sl-font-sans);
}

[data-component="app"] {
  grid-template-rows: min-content 1fr;
  height: 100vh;
  display: grid;
}

[data-component="app"] .app-header {
  height: var(--lookbook-toolbar-height);
  border-bottom: 1px solid var(--lookbook-divider-color);
  padding-inline: var(--sl-spacing-large);
  align-items: center;
  display: flex;
}

[data-component="app"] .app-header a {
  text-decoration: none;
}

[data-component="app"] .app-body {
  height: calc(100vh - var(--lookbook-toolbar-height));
}

[data-component="app"] .app-body > * {
  height: 100%;
}

[data-component="app"] .app-sidebar-layout {
  --divider-width: 1px;
  --min: 200px;
  --max: 400px;
  height: 100%;
}

[data-component="app"] .app-sidebar-layout::part(divider) {
  background-color: var(--lookbook-divider-color);
}

[data-component="app"] .app-sidebar-layout .app-sidebar {
  padding-top: var(--sl-spacing-medium);
  padding-bottom: var(--sl-spacing-medium);
  height: 100%;
  overflow: auto;
}

[data-component="app"] .app-sidebar-layout .app-main {
  height: 100%;
}

[data-component="inspector"] {
  width: 100%;
  height: 100%;
}

[data-component="inspector"] .inspector-layout {
  --divider-width: 1px;
  --min: 200px;
  --max: 600px;
  width: 100%;
  height: 100%;
}

[data-component="inspector"] .inspector-layout::part(divider) {
  background-color: var(--lookbook-divider-color);
}

[data-component="code"] {
  display: block;
}

[data-component="code"] pre, [data-component="code"] code {
  height: 100%;
  margin: 0;
  padding: 0;
  display: block;
}

[data-component="pane"] {
  width: 100%;
  height: 100%;
  position: relative;
}

[data-component="pane"] .pane-toolbar {
  height: var(--lookbook-toolbar-height);
  padding-right: var(--sl-spacing-2x-small);
  align-items: center;
  display: flex;
}

[data-component="pane"] .pane-toolbar sl-button-group {
  font-size: var(--lookbook-toolbar-icon-button-size);
  z-index: 1;
  margin-left: auto;
  position: relative;
}

[data-component="pane"] .pane-toolbar sl-icon-button {
  color: var(--lookbook-toolbar-icon-button-color);
}

[data-component="pane"] .pane-tabs {
  position: absolute;
  inset: 0;
}

[data-component="pane"] .pane-tabs sl-tab-group {
  height: 100%;
  position: relative;
  overflow: hidden;
}

[data-component="pane"] .pane-tabs sl-tab-group::part(tabs) {
  padding-right: 0;
}

[data-component="pane"] .pane-tabs sl-tab-group sl-tab-panel {
  inset: 0;
  top: calc(var(--lookbook-toolbar-height)  + var(--track-width));
  height: calc(100% - var(--lookbook-toolbar-height)  - var(--track-width));
  width: 100%;
  position: absolute;
  overflow: hidden;
}

[data-component="pane"] .pane-tabs sl-tab-group sl-tab-panel::part(base) {
  width: 100%;
  height: 100%;
  padding: 0;
  position: relative;
  overflow: hidden;
}

[data-component="panel"] {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

[data-component="panel"] .panel-toolbar {
  right: var(--sl-spacing-2x-small);
  top: var(--sl-spacing-x-small);
  border: 1px solid var(--lookbook-divider-color);
  background: #fff;
  border-radius: 8px;
  align-items: center;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  position: absolute;
}

[data-component="panel"] .panel-toolbar sl-button-group {
  font-size: var(--lookbook-toolbar-icon-button-size);
}

[data-component="panel"] .panel-toolbar sl-copy-button, [data-component="panel"] .panel-toolbar sl-icon-button {
  color: var(--lookbook-toolbar-icon-button-color);
}

[data-component="panel"] .panel-content {
  height: 100%;
  overflow: hidden;
}

[data-component="tab-group"] sl-tab-group {
  --track-width: 1px;
  --track-color: var(--lookbook-divider-color);
}

[data-component="tab-group"] sl-tab-group::part(active-tab-indicator) {
  transition: none;
}

[data-component="tab-group"] sl-tab::part(base) {
  height: var(--lookbook-toolbar-height);
  padding-block: 0;
}

[data-component="tab-group"] sl-tab-panel::part(base) {
  padding: 0;
}



[data-component="viewport"] {
  width: 100%;
  height: 100%;
}

[data-component="viewport"] .viewport-iframe {
  border: 0;
  width: 100%;
  height: 100%;
  margin: 0;
}





[data-component="panel"][data-panel="code"] .code-panel-code {
  height: 100%;
  overflow: auto;
}

[data-component="panel"][data-panel="code"] .code-panel-code [data-component="code"] {
  height: 100%;
}

[data-component="panel"][data-panel="code"] .code-panel-raw {
  display: none;
}







